import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import EditIcon from '@mui/icons-material/Edit';
import FileOpenIcon from '@mui/icons-material/FileOpen';
import {
  Box,
  Container,
  IconButton,
  Paper,
  TextField,
  Typography,
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import { useNavigate } from 'react-router-dom';
import rehypeDocument from 'rehype-document';
import remarkGfm from 'remark-gfm';

const MarkdownPlayground: React.FC = () => {
  const [markdownText, setMarkdownText] = useState<string>('');
  const [isEditorView, setIsEditorView] = useState<boolean>(true);

  const navigate = useNavigate();
  // Load saved content from session storage on initial render
  useEffect(() => {
    const savedMarkdown = sessionStorage.getItem('markdownContent');
    if (savedMarkdown) {
      setMarkdownText(savedMarkdown);
    }
  }, []);

  // Save markdown content to session storage whenever it changes
  useEffect(() => {
    sessionStorage.setItem('markdownContent', markdownText);
  }, [markdownText]);

  const handleMarkdownChange = (
    event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => {
    setMarkdownText(event.target.value);
  };

  const toggleView = () => {
    setIsEditorView((prev) => !prev);
  };
  const handleGoBack = () => {
    navigate('/');
  };

  return (
    <Container maxWidth="lg" style={{ marginTop: '2rem', height: '85vh' }}>
      <div style={{ position: 'absolute', top: 10, left: 16 }}>
        <IconButton onClick={handleGoBack} color="inherit">
          <ArrowBackIcon />
        </IconButton>
      </div>
      <Box
        mb={2}
        display="flex"
        justifyContent="space-between"
        alignItems="center"
      >
        <Typography variant="h4">Markdown Playground</Typography>
        <IconButton color="primary" onClick={toggleView}>
          {isEditorView ? <FileOpenIcon /> : <EditIcon />}
        </IconButton>
      </Box>

      <Box
        sx={{
          height: '100%',
          border: '1px solid #ccc',
          borderRadius: '8px',
          padding: '1rem',
          overflowY: 'auto',
        }}
      >
        {isEditorView ? (
          // Editor View
          <Box>
            <Typography variant="h6" gutterBottom>
              Edit Markdown
            </Typography>
            <TextField
              multiline
              rows={25}
              variant="outlined"
              fullWidth
              value={markdownText}
              onChange={handleMarkdownChange}
              placeholder="Write your markdown here..."
              InputProps={{
                style: { fontFamily: 'monospace' },
              }}
            />
          </Box>
        ) : (
          // Preview View
          <Box>
            <Typography variant="h6" gutterBottom>
              Markdown Preview
            </Typography>
            <Paper elevation={1} style={{ padding: '1rem', minHeight: '100%' }}>
              <ReactMarkdown remarkPlugins={[remarkGfm, rehypeDocument]}>
                {markdownText}
              </ReactMarkdown>
            </Paper>
          </Box>
        )}
      </Box>
    </Container>
  );
};

export default MarkdownPlayground;
